/*menu*/

ul{
    list-style: none;
}

a{
    cursor: pointer;
    color: white;
}

label {
    -webkit-tap-highlight-color: transparent;
}

section{
    height: 100vh;
}

nav {
    background: transparent;
    z-index: 16;
    margin: 0;
    position: absolute;
    width: 100%;
    transition: all 0.5s;
    top: 0;
    padding: 10px 0;
}


.checkbtn {
    font-size: 30px;
    color: white;
    float: right;
    line-height: 60px;
    margin-right: 40px;
    cursor: pointer;
    display: none;
}

#check {
    display: none;
}

.menu {
    width: 100%;
    font-weight: lighter;
    padding: 0;
    position: relative;
    padding-right: 100px;
    list-style: none;
    box-sizing: border-box;
    display: flex;
    justify-content: flex-end;
}


.logo {
    margin-top: 10px;
    position: absolute;
    left: 200px;
    height: 100px;
}

.slideUpButton{
    position: fixed;
    outline: none;
    bottom: 30px;
    right: 30px;
    width: 70px;
    height: 70px;
    border: none;
    border-radius: 50%;
    background: #3487dd;
    color:white;
    z-index: 20;
    transition: 0.3s all;
}

.slideUpButton:hover{
    background-color: grey;
}


.menuitem {
    margin-left: auto;
    display: inline-block;
    padding: 30px 10px;
    color: white;
    transition: all 0.5s;
}

.menu a {
    text-decoration: none;
    color: white;
    padding: 0 20px;
    font-size: 22px;
    font-weight: bold;
}

.menu_right {
    margin-right: 10%;
}

.menuitem:hover {
    color: #A3A7CD !important;
}

.menuclick {
    cursor: pointer;
}

.logo {
    color: white;
    font-size: 35px;
    line-height: 80px;
    padding: 0 100px 0 0;
    font-weight: bold;
}

.logo-mobile {
    display: none;
    height: 60px;
    margin-left: 30px;
    margin-top: 5px;
}

@media (max-width : 900px) {
    nav {
        height: 60px;
    }

    .logo {
        display: none;
    }

    .logo-mobile {
        display: block;
    }

    .menuitem a {
        font-size: 16px;
    }

    .checkbtn {
        display: block;
    }

    .menu {
        margin: 0;
        position: fixed;
        width: 100%;
        height: 100vh;
        float: right;
        display: block;
        z-index: 5;
        background: #3C3C4E;
        top: 80px;
        left: -100%;
        text-align: center;
        transition: all .5s;
    }

    .menuitem {
        display: block;
        margin: 0;
        line-height: 0;
        font-size: 20px;
    }

    .menu a:hover,
    .menu a.active {
        background: none;
        color: #603813;
        transition: all .5s;
    }

    #check:checked ~ .menu {
        left: 0;
    }
}

/*header*/
.header {
    background-position: center;
    background-size: auto 150vh;
}


/*slider*/

.sliderCont {
    position: relative;
    height: 100vh;
    width: 300%;
    display: flex;
    margin-left: -100%;
    transition: all .5s;

}

/*home*/
.mainHeading {
    text-shadow: 2px 2px black;
    font-size: 60px;
    margin-top: 0;
    padding-top: 240px;
    color: white;
    margin-left: 200px;
    
}



.homeScreen {
    width: 100%;
    background-image: url('images/bg.jpg');
    background-position: center;
    background-size: auto 150vh;
    min-height: 100vh;

}

.AI {
    color: #0088c8;
}

.moreInfo {
    font-size: 26px;
    margin-left: 200px;
    padding: 12px 30px;
    border-radius: 30px;
    outline: none;
    border: none;
    position: relative;
    background: #3487DD;
    color: white;
    cursor: pointer;
    transition: .3s all;
    -webkit-box-shadow: 0px 1px 5px 5px rgba(0, 0, 0, 0.4);
    -moz-box-shadow: 0px 1px 5px 5px rgba(0, 0, 0, 0.4);
    box-shadow: 0px 1px 5px 5px rgba(0, 0, 0, 0.4);
}

.slideHvr:hover::before {
    opacity: 1;
    width: 100%;
}

.slideHvr::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 0%;
    height: 100%;
    z-index: 1;
    border-radius: 29px;
    opacity: 0;
    background-color: rgba(255,255,255,0.5);
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}

/*services*/
.servicesScreen {
    width: 100%;
    color: white;
    background-image: url('images/bg2.jpg');
    background-position: center;
    height: fit-content;
    background-attachment: fixed;
    background-size: auto 150vh;
}

.servicesCont {
    min-height: 100vh;
    padding-top: 13%;
    box-sizing: border-box;
    justify-content: center;
    width: 100%;
    flex-wrap: wrap;
    display: flex;
}

.servicesBox {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10px;
    background: white;
    color: black;
    height: 360px;
    border-radius: 20px;
    width: 270px;
    transition: all .3s;
    -webkit-box-shadow: 0px 1px 5px 5px rgba(0, 0, 0, 0.4);
    -moz-box-shadow: 0px 1px 5px 5px rgba(0, 0, 0, 0.4);
    box-shadow: 0px 1px 5px 5px rgba(0, 0, 0, 0.4);
}

.servicesBox:hover {
    transform: scale(1.1)
}

.servicesBox:hover > div {
    background: #3487DD;
}

.servicesBox img {
    margin-top: 10px;
    display: block;
    width: 70%;
    object-fit: cover;
}

.servicesBox h2 {
    max-width: 50%;
    text-align: center;
    color: #3487DD;
}

.servicesBox div {
    cursor: pointer;
    transition: all .3s;
    margin-top: auto;
    height: 40px;
    background: #9e9e9e;
    width: 100%;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    text-align: center;
    color: white;
    font-size: 20px;
    line-height: 36px;
}

