/*contact_2nd_try*/

.contactLower{
    border-radius: 15px;
    position: relative;
    height: 500px;
    margin-top: 60px;
    background: white;
    -webkit-box-shadow: 0px 1px 5px 5px rgba(0, 0, 0, 0.4);
    -moz-box-shadow: 0px 1px 5px 5px rgba(0, 0, 0, 0.4);
    box-shadow: 0px 1px 5px 5px rgba(0, 0, 0, 0.4);
}

.contactUpper{
    position: absolute;
    top:50px;
    border-radius: 15px;
    left: -50px;
    height: 400px;
    width: 340px;
    background: #003a6a;
    -webkit-box-shadow: 0px 1px 5px 5px rgba(0, 0, 0, 0.4);
    -moz-box-shadow: 0px 1px 5px 5px rgba(0, 0, 0, 0.4);
    box-shadow: 0px 1px 5px 5px rgba(0, 0, 0, 0.4);
}

.contactUpper > h3{
    margin-left: 15px;
}

.contactUpper > div{
    display:flex;
    margin: 10px;
    margin-left: 15px;
}

.contactUpper > div > i{
    margin-right: 15px;
}

.contactForm{
    padding-left: 350px;
    padding-right: 60px;
    width: 450px;
    display: flex;
    flex-direction: column;
    color:black;
    padding-bottom: 50px;
}

.contactForm h3{
    padding-top: 20px;
    font-size: 24px;
    color: #003a6a;
}

.contactForm p {
    margin-top: 0;
    font-size: 12px;
    color: black;
    opacity: 0.6;
}

.contactForm input{
    margin-bottom: 10px;
    background: #e4e7ec;
    width: 90%;
    border:none;
    outline:none;
    border-radius: 10px;
    padding: 14px;
    display: block;
}

.contactForm textarea{
    width: 90%;
    font-family: 'Roboto', sans-serif;
    padding:14px;
    height: 100px;
    resize: none;
    border:none;
    outline: none;
    border-radius: 10px;
    background: #e4e7ec;
    margin-bottom: 20px;
}

.submitButton{
    background: #3487dd !important;
    border-radius: 20px !important;
    width: 140px !important;
    color: white;
    transition: .3s all;
    -webkit-box-shadow: 0px 1px 1px 1px rgba(0, 0, 0, 0.4);
    -moz-box-shadow: 0px 1px 1px 1px rgba(0, 0, 0, 0.4);
    box-shadow: 0px 1px 1px 1px rgba(0, 0, 0, 0.4);
}

.submitButton:hover{
    background: grey !important;
}

.extraInfo{
    font-size: 10px;
    opacity: 0.6;
    display: flex;
    position: absolute;
    bottom: 10px;
   justify-content: flex-end;
    flex-direction: column;
}

/*references*/

.referencesScreen {
    height: 100vh;
    width: 100%;
    color: white;
    background-image: url('images/bg2.jpg');
    background-position: center;
    background-size: auto 150vh;
}

.referencesChooser{
    margin-top: 170px;
    width: 75%;
