}

.chooserButton{
    color: white;
    cursor: pointer;
    margin-right: 20px;
    border:none;
    outline: none;
    padding: 13px;
    transition: all .3s;
    font-weight: bold;
    border-radius: 18px;
    font-size: 15px;
    background: #9e9e9e;
}

.chooserButton:hover{
    background: #A3A7CD !important;
}

.referencesWrapper{
    width: 90%;
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
}

.referencesCont{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin:15px;
    width: 350px;
